import Link from 'next/link';

export default function MegaWhoWeServe({ close }: { close: () => void }) {
  return (
    <div className="absolute left-0 top-full w-screen bg-white border-t shadow-xl" onMouseLeave={close}>
      <div className="max-w-7xl mx-auto px-8 py-10 grid grid-cols-4 gap-10">
        <Column title="Industries" links={['Healthcare', 'Fintech', 'SaaS', 'E-commerce']} />

        <Column title="Company Size" links={['Startups', 'SMBs', 'Enterprise']} />

        <Column title="Use Cases" links={['Growth', 'Automation', 'Scaling']} />

        {/* Featured Card */}
        <div className="bg-gray-50 rounded-lg p-6">
          <h4 className="font-semibold mb-2">Featured</h4>
          <p className="text-sm text-gray-600 mb-4">See how teams scale with our platform.</p>
          <Link href="#" className="text-blue-600 font-medium text-sm">
            View Success Stories →
          </Link>
        </div>
      </div>
    </div>
  );
}

function Column({ title, links }: { title: string; links: string[] }) {
  return (
    <div>
      <h3 className="text-xs uppercase font-semibold text-gray-500 mb-4">{title}</h3>
      <ul className="space-y-3">
        {links.map((link) => (
          <li key={link}>
            <Link href="#" className="text-sm hover:text-blue-600">
              {link}
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
}
