'use client';

import { Navbar, NavbarBrand, NavbarToggle, Button } from 'flowbite-react';
import Link from 'next/link';
import { useState } from 'react';

import Logo from './Logo';
import NavTrigger from './NavTrigger';
import MegaWhoWeServe from './MegaWhoWeServe';
import MegaCaseStudies from './MegaCaseStudies';

export type DropdownKey = 'who' | 'solutions' | null;

export default function Header() {
  const [openDropdown, setOpenDropdown] = useState<DropdownKey>(null);
  const [mobileOpen, setMobileOpen] = useState(false);

  return (
    <>
      {/* ================= NAVBAR ================= */}
      <Navbar
        fluid
        className="fixed top-0 left-0 right-0 z-50 bg-white/90 backdrop-blur border-b"
        onMouseLeave={() => setOpenDropdown(null)}
      >
        <div className="max-w-7xl mx-auto w-full px-4 flex items-center justify-between">
          <NavbarBrand as={Link} href="/">
            <Logo />
          </NavbarBrand>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center gap-8">
            <NavTrigger label="Who We Serve" onHover={() => setOpenDropdown('who')} />
            <NavTrigger label="Case Studies" onHover={() => setOpenDropdown('solutions')} />

            <Link href="/blogs" className="text-sm font-medium hover:text-blue-600">
              Blogs
            </Link>
          </div>

          {/* CTA */}
          <div className="hidden md:flex">
            <Button outline>Get Started</Button>
          </div>

          {/* Mobile Toggle */}
          <NavbarToggle onClick={() => setMobileOpen(!mobileOpen)} />
        </div>
      </Navbar>

      {/* ================= MEGA MENUS ================= */}
      <div className="relative z-40">
        {openDropdown === 'who' && <MegaWhoWeServe close={() => setOpenDropdown(null)} />}

        {openDropdown === 'solutions' && <MegaCaseStudies close={() => setOpenDropdown(null)} />}
      </div>

      {/* ================= MOBILE MENU ================= */}
      {mobileOpen && (
        <div className="fixed inset-0 z-40 bg-white pt-24 px-6 md:hidden">
          <Link href="/blogs" className="block py-4 text-lg">
            Blogs
          </Link>
          <Button className="w-full mt-4">Get Started</Button>
        </div>
      )}
    </>
  );
}
