import Link from 'next/link';

export default function MegaCaseStudies({ close }: { close: () => void }) {
  return (
    <div className="absolute left-0 top-full w-screen bg-white border-t shadow-xl" onMouseLeave={close}>
      <div className="max-w-7xl mx-auto px-8 py-10 grid grid-cols-3 gap-10">
        <Column title="By Industry" links={['Healthcare', 'Fintech', 'Retail']} />
        <Column title="By Size" links={['Startups', 'Mid-Market', 'Enterprise']} />

        {/* Highlight */}
        <div className="bg-black text-white rounded-lg p-6">
          <h4 className="font-semibold mb-2">Latest Case Study</h4>
          <p className="text-sm opacity-80 mb-4">How a SaaS company grew 4× in 6 months.</p>
          <Link href="#" className="font-medium text-sm">
            Read Story →
          </Link>
        </div>
      </div>
    </div>
  );
}

function Column({ title, links }: { title: string; links: string[] }) {
  return (
    <div>
      <h3 className="text-xs uppercase font-semibold text-gray-500 mb-4">{title}</h3>
      <ul className="space-y-3">
        {links.map((link) => (
          <li key={link}>
            <Link href="#" className="text-sm hover:text-blue-600">
              {link}
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
}
